@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
body{overflow-x:hidden;width:100vw;height:100vh;background-color:#0a021d !important;background-image:radial-gradient(circle at top right, rgba(121,68,154,0.13)),radial-gradient(circle at 20% 80%, rgba(41,196,255,0.13), transparent)}canvas{position:fixed;width:100vw;height:100vh}a{position:absolute;bottom:2vmin;right:2vmin;color:rgba(255,255,255,0.2);text-decoration:none}a:hover{color:#fff}

#projetos {
  height: 100vh;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
  
}

#projetos h1 {
  text-align: center;
  font-size: 3rem;
  font-weight: 200;
  margin-top: 100px;
  margin-bottom: 50px;

  color: #fff;
  font-family:Roboto;
  width: 100%;
  padding: 20px;
    }

.container body{
  display:-webkit-flex;
  display:flex;
  margin:0;
  padding:0;
  min-height: 100vh;
  background: rgba(206, 99, 99, 0.637);
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: arial;
}

.container{
  width: 800px;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap:wrap;
          flex-wrap:wrap;
  
}

.container .card11{
  position: relative;
}

.container .card11 .face{
  width:300px;
  height: 200px;
  transition:.4s;
  
}

.container .card11 .face.face1{
  position: relative;
  background: #060411; /*cor da frente do card ------------------   */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content:center;
          align-content:center;
  -webkit-align-items: center;
          align-items: center;
  z-index: 1;
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
  font-family:Roboto;
}

.container .card11:hover .face.face1{
  -webkit-transform: translateY(0);
          transform: translateY(0);
  box-shadow:
    inset 0 0 60px whitesmoke,
    inset 20px 0 80px #f0f,
    inset -20px 0 80px #0ff,
    inset 20px 0 300px #f0f,
    inset -20px 0 300px #0ff,
    0 0 50px #fff,
    -10px 0 80px #f0f,
    10px 0 80px #0ff;
   
}


.container .card11 .face.face1 .content11{
  opacity: .2;
  transition:  0.5s;
  text-align: center;
   

}

.container .card11:hover .face.face1 .content11{
  opacity: 1;
 
}

.container .card11 .face.face1 .content11 i{
  font-size: 3em;
  color: white;
  display: inline-block;
  font-family:Roboto;

   
}

.container .card11 .face.face1 .content11 h3{
  font-size: 1em;
  color: white;
  text-align: center;
  

}

.container .card11 .face.face1 .content11 a{
   transition: .5s;
}

.container .card11 .face.face2{
   position: relative;
   background:#1b132c75;
   display: -webkit-flex;
   display: flex;
   -webkit-align-items: center;
           align-items: center;
   -webkit-justify-content: center;
           justify-content: center;
   padding: 20px;
  box-sizing: border-box;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.808);
  -webkit-transform: translateY(-100px);
          transform: translateY(-100px);
  font-family:Roboto;

}

.container .card11:hover .face.face2{
    -webkit-transform: translateY(0);
            transform: translateY(0);

}

.container .card11 .face.face2 .content11 p, a{
  font-size: 10pt;
  margin: 0 ;
  padding: 0;
  color:rgb(255, 255, 255);
}

.container .card11 .face.face2 .content11 a{
  text-decoration:none;
  color: rgb(238, 234, 234);
  box-sizing: border-box;
  padding: 10px;
  margin: 15px 0 0;
  display: inline-block;

}

.container .card11 .face.face2 .content11 a:hover{
  background:#1b132c ;
  color: whitesmoke; 
  box-shadow: inset 0px 0px 10px rgba(185, 68, 200, 0.904);
  font-family:Roboto;

}

@media (max-width: 770px) {
  #projetos {
    margin-top: 20%;
    margin-left: 14vh;
  }
}



